@use "./utils/variables" as v;

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  max-width: 100vw;
  height: fit-content;
  background: v.$color1Dark;
}
.container {
  margin: 0 auto;
  max-width: 1790px;
  padding: 0 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
